@import '../../styles/variables';

.serviceMsg {
    width: 100%;
    padding-top: 15%;
    text-align: center;
    font-size: 28px;
}

.mainTitle {
    margin-bottom: 24px;
    font-family: $fontFamily;
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: left;
    color: $black;
}

.descr {
    margin-bottom: 22px;
    font-family: $fontFamily;
    font-size: 14px;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: 0.01em;
    text-align: left;
    color: $grey;
}

.container {
    margin: 0 auto;
    padding: 16px 13px;
    max-width: 333px;

    @media (min-width: 1380px) {
        max-width: 1380px;
        padding: 32px 42px;
    }
}