@import '../../styles/variables';

.filter {
    position: absolute;
    top: 45px;
    right: 60px;
}

.filter {
    div {
        display: flex;
        align-items: center;
    }
}

.isAvailable {
    display: inline-block;
    margin-right: 10px;
}

.btn, .filterBtn {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid $black;
    cursor: pointer;
}

.filterBtn {
    background-image: url('../../image/check-mark.png');
    background-repeat: no-repeat;
    background-size: cover;
}