@import '../../styles/variables';

.item {
    width: 305px;
    margin-bottom: 16px;
    box-shadow: 0px 10px 14px 0px #00000012;
    border-radius: 8px;

    @media (min-width: 1380px) {
        margin-bottom: 25px;
    }
}

.item:not(:nth-child(4n)) {
    @media (min-width: 1380px) {
        margin-right: 25px;
    }
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 26px;
    width: 100%;
    height: 404px;
    color: $white;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 58.68%, rgba(0, 0, 0, 0.5) 100%);
    background-image: url('../../image/img.png');
    background-repeat: no-repeat;
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
}

.top {
    span {
        display: block;
    }
}

.createdBy {
    margin-bottom: 5px;
    font-family: $fontFamily;
    font-size: 12px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;    
}

.name {
    font-family: $fontFamily;
    font-size: 14px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
}

.info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 78px;
    padding: 16px;
}

.left, .right {
    span {
        display: block;
    }

    .available, .price {
        margin-bottom: 10px;
    }
}

.bottom {
    span {
        display: block;
        font-family: $fontFamily;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0em;
        text-align: left;
    }
}

.right {
    span {
        font-family: $fontFamily;
        line-height: 24px;
        letter-spacing: 0em;
        text-align: right;
    }
}

.price, .available {
    font-size: 14px;
    font-weight: 400;
    color: $grey80;
}

.priceCount, .availableCount {
    font-size: 16px;
    font-weight: 600;
    color: #0040E5;
}

.availableCount {
    color: $black;
}